.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}
.section-title {
	margin-top: 30px;
}

.card {
	width: calc(33% - 10px);
	margin: 20px 0;
}
.card-link {
	color: inherit;
	text-decoration: none;
}
.card-image {
	width: 100%;
}
.channel-image {
	border-radius: 100%;
	padding: 0, 10px, 0, 0;
	width: 40px;
	height: 40px;
}
.card-title {
	margin-top: 10px;
	margin-bottom: 0;
}
.card-channel {
	margin-top: 5px;
	margin-bottom: 5px;
	font-size: 14px;
}

/* Tablets */
@media (max-width: 1000px) {
	.App {
		max-width: 600px;
	}
	.card {
		width: calc(50% - 22px);
	}
}

/* Mobiles */
@media (max-width: 640px) {
	.App {
		max-width: 100%;
		padding: 0 15px;
	}
	.card {
		width: 100%;
	}
}
@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
